/*
 * zhouxing
 * 手机端常用组件
 * 不依赖第三方js库
 * */
export default(function() {

	//禁止默认事件
	function touchDefault(e) {
		e.stopPropagation();
		e.preventDefault();
	}

	//禁止touch事件
	function stopTouchEvent() {
		document.addEventListener('touchstart', touchDefault, false);
		document.addEventListener('touchmove', touchDefault, false);
		document.addEventListener('touchend', touchDefault, false);
	}

	//启动touch事件
	function startTouchEvent() {
		document.removeEventListener('touchstart', touchDefault, false);
		document.removeEventListener('touchmove', touchDefault, false);
		document.removeEventListener('touchend', touchDefault, false);
	}

	var controller = {
		//选择窗
		confirmBox: function(params, callBack) {
			if(!params.title) {
				params.title = '提示';
			}

			if(!params.content) {
				params.content = '请输入提示内容';
			}

			if(!params.leftBtn) {
				params.leftBtn = '取消';
			}

			if(!params.rightBtn) {
				params.rightBtn = '确定';
			}

			//禁止滑动
			document.addEventListener('touchmove', touchDefault, false);

			var mybox = document.querySelector("div.dialog-box");
			if(mybox) {
				document.body.removeChild(mybox);
			}

			var alertParent = document.createElement("div"); //父节点
			alertParent.className = "zx-mask dialog-box"; //classname
			var alert_div = document.createElement("div"); //div节点

			//标题节点
			var alert_h1 = document.createElement("h1"); //h1节点
			var title = document.createTextNode(params.title); //h1文本
			alert_h1.appendChild(title);

			//内容节点
			var alert_p = document.createElement("p"); //p节点
			var ptext = document.createTextNode(params.content); //p文本
			alert_p.appendChild(ptext);

			//按钮节点
			var alert_a = document.createElement("a"); //a节点
			var box_i2 = document.createElement("i"); //a节点
			var itext2 = document.createTextNode(params.leftBtn); //a文本
			box_i2.appendChild(itext2);
			alert_a.appendChild(box_i2);

			var box_i1 = document.createElement("i"); //a节点
			var itext1 = document.createTextNode(params.rightBtn); //a文本
			box_i1.appendChild(itext1);
			alert_a.appendChild(box_i1);

			//添加到div节点
			alert_div.appendChild(alert_h1);
			alert_div.appendChild(alert_p);
			alert_div.appendChild(alert_a);

			//添加到父节点
			alertParent.appendChild(alert_div);

			//添加到body
			document.body.appendChild(alertParent);

			//点击事件-确定
			box_i1.addEventListener("touchend", function(e) {
				e.preventDefault();
				e.stopPropagation();
				document.body.removeChild(alertParent);
				document.removeEventListener('touchmove', touchDefault, false);
				if(callBack) {
					callBack(1);
				}
			}, true);

			//点击事件-取消
			box_i2.addEventListener("touchend", function(e) {
				e.preventDefault();
				e.stopPropagation();
				document.body.removeChild(alertParent);
				document.removeEventListener('touchmove', touchDefault, false);
				if(callBack) {
					callBack(0);
				}
			}, true);
		},

		//提示窗
		alertBox: function(content, callBack) {
			//禁止滑动
			document.addEventListener('touchmove', touchDefault, false);

			var mybox = document.querySelector("div.dialog-box");
			if(mybox) {
				document.body.removeChild(mybox);
			}

			var alertParent = document.createElement("div"); //父节点
			alertParent.className = "zx-mask dialog-box"; //classname
			var alert_div = document.createElement("div"); //div节点

			//标题节点
			var alert_h1 = document.createElement("h1"); //h1节点
			var title = document.createTextNode("提示"); //h1文本
			alert_h1.appendChild(title);

			//内容节点
			var alert_p = document.createElement("p"); //p节点
			var ptext = document.createTextNode(content); //p文本
			alert_p.appendChild(ptext);

			//按钮节点
			var alert_a = document.createElement("a"); //a节点
			var atext = document.createTextNode("确定"); //a文本
			alert_a.appendChild(atext);

			//添加到div节点
			alert_div.appendChild(alert_h1);
			alert_div.appendChild(alert_p);
			alert_div.appendChild(alert_a);

			//添加到父节点
			alertParent.appendChild(alert_div);

			//添加到body
			document.body.appendChild(alertParent);

			//点击事件
			alert_a.addEventListener("touchend", function(e) {
				e.preventDefault();
				e.stopPropagation();
				document.body.removeChild(alertParent);
				document.removeEventListener('touchmove', touchDefault, false);
				if(callBack) {
					callBack();
				}
			}, false);
		},

		//toast
		toastBox: function(content) {
			//禁止touch
			stopTouchEvent();
			var toastBox = document.querySelector("div.toast-box");
			if(toastBox) {
				return;
			}

			var toast_div = document.createElement("div"); //父节点
			toast_div.className = "toast-box"; //classname
			var toast_p = document.createElement("p"); //子节点p
			var p_node = document.createTextNode(content); //text文本
			toast_p.appendChild(p_node);
			toast_div.appendChild(toast_p);
			document.body.appendChild(toast_div);
			setTimeout(function() {
				document.body.removeChild(toast_div);
				startTouchEvent();
			}, 3000);
		},

		/*******************菊花loading效果********************/
		loadingFlower: function(content) {
			/* 居中加载框; */

			//禁止touch
			stopTouchEvent();

			var loadingBox = document.querySelector("div.loadindBox");
			if(loadingBox) {
				document.body.removeChild(loadingBox);
			}

			var dom_div = document.createElement("div"); //父节点
			var dom_div1 = document.createElement("div"); //子div

			dom_div1.innerHTML = '<div><span></span><span></span><span></span>' +
				'<span></span><span></span><span></span><span></span><span></span>' +
				'<span></span><span></span><span></span><span></span></div>';

			//有提示文字
			if(content) {
				var dom_p = document.createElement("p"); //p
				var p_node = document.createTextNode(content); //text文本
				dom_p.appendChild(p_node);
				dom_div1.appendChild(dom_p);
			}

			dom_div.appendChild(dom_div1);
			dom_div.className = "zx-mask loadindBox"; //classname

			//添加到body
			document.body.appendChild(dom_div);
		},

		//关闭loadingFlower动画
		closeLoadingFlower: function() {
			var loadingBox = document.querySelector("div.loadindBox");
			if(loadingBox) {
				startTouchEvent();
				// loadingBox.className += " clearloadBounce";
				// setTimeout(function () {
				document.body.removeChild(loadingBox);
				// }, 1000);
			}
		},

		/**********************页面loadingBounce动画***************************/
		loadingBounce: function(content) {
			//禁止touch
			stopTouchEvent();

			var loadingBox = document.querySelector("div.loadingBounce");
			if(loadingBox) {
				document.body.removeChild(loadingBox);
			}

			var dom_div = document.createElement("div"); //父节点

			var dom_div1 = document.createElement("div");
			dom_div1.innerHTML = "<div></div><div></div>"
			dom_div.appendChild(dom_div1);

			//有提示文字
			if(content) {
				var dom_p = document.createElement("p"); //p
				var p_node = document.createTextNode(content); //text文本
				dom_p.appendChild(p_node);
				dom_div.appendChild(dom_p);
			}

			dom_div.className = "zx-mask loadingBounce"; //classname

			//添加到body
			document.body.appendChild(dom_div);
		},

		//关闭loadingBounce动画
		closeLoadingBounce: function() {
			var loadingBox = document.querySelector("div.loadingBounce");
			if(loadingBox) {
				startTouchEvent();
				document.body.removeChild(loadingBox);
			}
		},

		/**********************查看图片***************************/
		//body加入以下dom结构
		// <div id="lookImage">
		// <div><div class="loadingImages"><div><span></span></div></div></div>
		// <!--<div><p><img src="../images/test.png" /></p></div>-->
		// </div>

		LookImage: function(imgurl) {
			var lookImage = document.getElementById("lookImage");
			lookImage.style.display = "block";

			var nowImg = new Image();

			nowImg.onload = function() {
				setTimeout(function() {
					var myImages = '<div><p><img src="' + nowImg.src + '" /></p></div>';

					lookImage.innerHTML = myImages;
				}, 500);
			};

			nowImg.src = imgurl;
		},

		CloseLookImage: function() {
			//关闭查看图片
			var lookImage = document.getElementById("lookImage");

			//点击事件-取消
			lookImage.addEventListener("touchend", function(e) {
				e.stopPropagation();
				e.preventDefault();

				var loadHtml = '<div><div class="loadingImages"><div><span></span></div></div></div>';

				this.style.display = "none";
				this.innerHTML = loadHtml;
			});
		},

	};

	return controller;
}());


